/* Contact Section */

#container-contact {
    background-image: linear-gradient(130deg,
            hsl(38deg 96% 39%) 0%,
            hsl(37deg 92% 37%) 0%,
            hsl(37deg 88% 35%) -1%,
            hsl(37deg 84% 33%) -1%,
            hsl(36deg 81% 31%) -1%,
            hsl(36deg 78% 29%) -1%,
            hsl(35deg 75% 26%) -1%,
            hsl(35deg 73% 24%) -1%,
            hsl(35deg 70% 22%) 0%,
            hsl(34deg 67% 19%) 0%,
            hsl(34deg 64% 17%) 2%,
            hsl(34deg 60% 15%) 4%,
            hsl(34deg 58% 12%) 7%,
            hsl(34deg 57% 10%) 12%,
            hsl(35deg 61% 7%) 20%,
            hsl(33deg 68% 4%) 35%,
            hsl(0deg 0% 0%) 99%);
    position: relative;
    width: 100svw;
    height: 100vh;
    /* or any desired height */
    overflow-x: hidden;
}

/* Flexbox container overlay */
