.close-btn {
    display: flex;
    position: fixed;
    width: 25px;
    height: 25px;
    z-index: 1200;
    top: 15px;
    right: 15px;
    border: 1.5px solid white;
    border-radius: 1.5px;
    /* White border */
    background-color: transparent;
    /* Transparent background */
    cursor: pointer;
    box-sizing: border-box;
    opacity: 0;
    /* Initially hidden */
    transition: opacity 2.0s ease;
    /* Transition for fade-in */
}

/* The 'X' (cross) inside the button */
.close-btn::before,
.close-btn::after {
    content: '';
    position: absolute;
    width: 80%;
    /* Slightly smaller than the button size */
    height: 1.5px;
    /* Thickness of the cross lines */
    background-color: white;
    /* White cross */
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) rotate(45deg);
    /* First line rotated */
}

.close-btn::after {
    transform: translate(-50%, -50%) rotate(-45deg);
    /* Second line rotated */
}


.close-btn.show {
    /* display: flex; */
    opacity: 1;
}



